.getStarted{
    width: 100%;
    height: 447px;
    background: linear-gradient(229.32deg, #6888FF 12.77%, #5E80FF 73.11%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.getStartedClouds{
    position: absolute;
    width: 100%;
}
.getStarted-text{
    width: 712px;
    height: 194px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.getStarted-plan{
    width: 90px;
    height: 12px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #FFFFFF;
    
}
.getStarted-get{
    width: 358px;
    height: 60px;
    font-family: 'Quicksand';
    font-style: normal;
    font-weight: 300;
    font-size: 48px;
    line-height: 60px;
    text-align: center;
    color: #FFFFFF;
    white-space: nowrap;
    margin-top: 16px;
}
.getStarted-brute{
    width: 712px;
    height: 78px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 29px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #FFFFFF;
    margin-top: 28px;
}
.getStarted-btns{
    display: flex;
    margin-top: 28px;
}
.getStarted-btn1{
    width: 130.08px;
    height: 43px;
    background: #FFFFFF;
    border: 0px solid #A0B4FF;
    font-family: 'Quicksand';
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 16px;
    align-items: center;
    text-align: center;
    color: #565F82;
}
.getStarted-btn2{
    box-sizing: border-box;
    width: 155px;
    height: 43px;
    
    border: 1px solid #A0B4FF;

    font-family: 'Quicksand';
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    align-items: center;
    text-align: center;
    background-color: #6888FF;
    color: #C7D3FF;
}